// Criar componentes é criar funções criam tags do HTML
function Titulo(props) { // Componente
  return <h1 color={props.cor}>Titulo da página é: {props.children}</h1>
  // return `<h1>Titulo da página é: ${props.conteudoInterno}</h1>`
}


function HomePage() { // Componente
  return (
    <div>
      <Titulo cor={'red'}>Home</Titulo>

      <a href="/sobre">Sobre</a>
    </div>
  );
}

export default HomePage;